  position: absolute;
  -webkit-animation: loading-dot-fade 1.5s infinite ease;
          animation: loading-dot-fade 1.5s infinite ease;
}
.icon__loading-dots .dot:nth-child(1) {
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.icon__loading-dots .dot:nth-child(2) {
  top: 0;
  bottom: 0;
  left: 0;
