.rotator .knob {
  width: 70%;
  padding-bottom: 70%;
  border-radius: 50%;
  background: #2E2E2E;
  display: inline-block;
  cursor: -webkit-grab;
  cursor: -moz-grab;
  position: relative;
}
.rotator .knob .grabbing {
  cursor: -webkit-grabbing;
  cursor: -moz-grabbing;
}
.rotator .knob:after {
  content: "";
  width: 2px;
  height: 7px;
  background: #ffffff;
  position: absolute;
  top: 3px;
  left: 50%;
  margin-left: -1px;
}
.rotator .knob:before {
  content: "";
  width: 100%;
  height: 100%;
  border: 2px solid #EB1E23;
  position: absolute;
  top: -2px;
  left: -2px;
  border-radius: 50%;
  display: none;
}
.rotator input:focus + .knob-container .knob:before {
  display: block;
}
.rotator .item-value {
  background: 0;
  border: 0 none;
  width: 100%;
  text-align: center;
  color: #ffffff;
}
.rotator .knob-container {
  position: relative;
  text-align: center;
}
.rotator .knob-container .value-indicator {
  width: 1px;
  top: -4px;
  bottom: -4px;
  position: absolute;
  left: 0;
  right: 0;
  background: #C3C3C3;
  margin: auto;
}
.rotator .knob-container .value-indicator:first-child {
  bottom: 3px;
}
.rotator .knob-container .value-indicator + .value-indicator {
  -webkit-transform: rotate(30deg);
          transform: rotate(30deg);
}
.rotator .knob-container .value-indicator + .value-indicator + .value-indicator {
  -webkit-transform: rotate(60deg);
          transform: rotate(60deg);
}
.rotator .knob-container .value-indicator + .value-indicator + .value-indicator + .value-indicator {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.rotator .knob-container .value-indicator + .value-indicator + .value-indicator + .value-indicator + .value-indicator {
  -webkit-transform: rotate(120deg);
          transform: rotate(120deg);
}
.rotator .knob-container .value-indicator + .value-indicator + .value-indicator + .value-indicator + .value-indicator + .value-indicator {
  -webkit-transform: rotate(150deg);
          transform: rotate(150deg);
}
